.resume-container
{
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resume-container h2 
{
  font-size: 45px;
}


.resume-download-button
{
  background-color: #000099;
  color: #faf9f6;
  width: 300px;
  padding: 8px 24px;
  margin: auto;
  font-size: 20px;
  border: 0;
  border-radius: 24px 24px;
  text-decoration: none;
  display: flex;
  justify-content: center;
}

.resume-link {
  color: #faf9f6;
  margin:auto;
  text-decoration: none;
  font-size: 30px;
}